.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 2px solid var(--twitter-background) ;
    padding-bottom: 10px;
}

.post_headerText{
    margin: 10px;
}
.post_body > img{
    border-radius: 20px;
    width: 100% !important;

}

.post_footer{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post_badge{
    font-size: 14px !important;
    color: var(--twitter-color);
}

.post_headerSpecial{
    font-weight: 600;
    font-size: 12px;
    color: grey;
}

post_headerDescription{
    margin-bottom: 10px;
    font-size: 15px;
}

.post_body{
    flex: 1;
    padding:10px
}

.post_avatar{
    padding: 20px;
}